/* ------------------------Card--------------------------- */

.card{
  /* border: solid #2F4050 3px; */
  height: 7%;
  width: 100%;
  margin: 1.5%;
  align-items: center;
  /* margin: 20px; */
  border: 4px solid #3d6681;
}
.about{
  display: inline-block;
}
.infoLayer img{
  height: 60px;
  /* border: 2px solid green; */
}
.infoLayer{
  border-bottom: 2px solid #3d6681;
