  height: 18%;
}

.infoLayer h2{
  
  /* border: 2px solid green; */
  text-align: center;
}
.mainLayer{
  /* border: rgb(250, 250, 200) 3px solid;
  border: solid rgb(250, 250, 200) 0.5px; */
