  /* overflow-y: scroll; */
}


/*-----------------------Header--------------------------- */

.header{
  background-color: #3d6681; 
  color: white;
  padding: 1px;
  position: sticky;
  top: 0;
  border-bottom: #3f576b 1px solid;
}

.header img{
  height: 100px;
  margin: 0;
  margin-left: 40px;
  /* border: black 3px solid; */
}

.header h1{
  position: absolute;
  margin-left: 175px;
  /* border: black 3px solid; */
}

.headerElement{
  display: inline-block;
  padding-top: 12px;
  /* border: black 3px solid; */
}

/* ------------------------Sidebar--------------------------- */

.sidebar{
  height: 3.6%;
  background-color: #3d6681;  
  color: white;
  position: sticky;
  top: 119px;
} 

.sidebarlist{
  width: 100%;
  height: 100%;
  padding: 0;
  /* border: 3px solid black; */
  
}
